@import '../../../index.scss';

.form {
  margin: auto;
  border-radius: 6px;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 25rem;
  overflow: hidden;

  .modalHead {
    width: 100%;
    height: 4rem;
    background-color: $purple;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 1.5rem;
    font-weight: bolder;
    margin-bottom: 1rem;
  }

  .formGroup {
    width: 22rem;
    margin: 0rem 0rem 0.5rem 0rem;

    .formControl {
      border: 1px solid $light-gray;
      height: 2rem;
      border-radius: 5px;
      padding: 0rem 0.5rem;
      color: $gray;
    }
  }

  div {
    display: flex;
    flex-direction: column;

    .error {
      color: red;
      margin: 0 0 0.6rem 0;
      padding: 0;
    }
    input {
      margin: 0.6rem 0;
      width: 20rem;
      height: 1.5rem;
      border-radius: 5px;
      border: 1px solid $light-gray;
      padding: 0.3rem 1rem;
    }
  }

  .checkbox {
    display: flex;
    flex-direction: row;
    justify-content: left;
    text-align: left;
    width: 20rem;
    label {
      margin: 0.5rem;
      display: inherit;
      justify-content: center;
      align-items: center;
    }
    input {
      margin-left: -1rem;
      width: 1rem;
      display: inherit;
      justify-content: left;
    }
  }
  .button {
    background-color: $purple;
    margin: 1rem 0rem 1.5rem 0rem;
    text-decoration: none;
    color: white;
    padding: 0.6rem 1rem;
    font-weight: bolder;
    cursor: pointer;
    border: 1px solid $purple;
    width: 22rem;
    border-radius: 5px;

    &:hover {
      background-color: white;
      color: $purple;
    }
  }
}
